.content_sec {
    padding-top: 50 / $setFs + rem;

    @media screen and (max-width: 480px) {
        padding-top: 30 / $setFs + rem;
    }
}

.content_paper {
    box-shadow: 1 / $setFs + rem 2 / $setFs + rem 5 / $setFs + rem rgba(0, 0, 0, 0.25);
    padding: 60 / $setFs + rem 80 / $setFs + rem 80 / $setFs + rem;
    background: #fff;
    position: relative;

    @media screen and (max-width: 768px) {
        padding: 60 / $setFs + rem 40 / $setFs + rem 80 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        margin-left: -10 / $setFs + rem;
        margin-right: -10 / $setFs + rem;
        padding: 40 / $setFs + rem 10 / $setFs + rem 60 / $setFs + rem;
    }
}

.content_top_row {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 10 / $setFs + rem;

    @media screen and (max-width: 768px) {
        margin-bottom: 20 / $setFs + rem;
    }
}

.content_date {
    @extend .duration;
    position: relative;
    bottom: -2 / $setFs + rem;
}

.content_cate_row {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10 / $setFs + rem;

    @media screen and (max-width: 768px) {
        margin-bottom: 30 / $setFs + rem;
    }
}

.cate_tag {
    @extend .tag;
}

.content_share {
    display: flex;
    justify-content: flex-end;
}

.socialBtn {
    width: 32 / $setFs + rem;
    height: 32 / $setFs + rem;
    margin-left: 12 / $setFs + rem;
    position: relative;

    &:first-child {
        margin-left: 0;
    }

    @media screen and (max-width: 768px) {
        width: 34 / $setFs + rem;
        height: 34 / $setFs + rem;
        margin-left: 10 / $setFs + rem;

        &:first-child {
            margin-left: 0;
        }
    }
}

.socialBtn_link {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 19 / $setFs + rem;
    border: 1 / $setFs + rem solid rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    @include acc;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (19 + 3) / $setFs + rem;
    }

    @media screen and (max-width: 768px) {
        font-size: 20 / $setFs + rem;
    }

    i {
        position: relative;
        top: 1 / $setFs + rem;
    }

    &.link {
        font-size: 16 / $setFs + rem;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (16 + 3) / $setFs + rem;
        }
    }

    &:hover {
        color: #fff;

        &.fb {
            background: #3b5998;
            border: 1 / $setFs + rem solid #3b5998;
        }

        &.twitter {
            background: #1da1f2;
            border-color: solid #1da1f2;
        }

        &.line {
            background: #00c300;
            border-color: #00c300;
        }

        &.weixin {
            background: #7bb32e;
            border-color: #7bb32e;
        }

        &.weibo {
            background: #df2029;
            border-color: #df2029;
        }

        &.mail {
            background: $color;
            border-color: $color;
        }

        &.whatsapp {
            background: #25d366;
            border-color: #25d366;
        }

        &.link {
            background: #de9100;
            border-color: #de9100;
        }
    }
}

.content_author {
    padding-left: 30 / $setFs + rem;
    margin-left: 80 / $setFs + rem;
    margin-top: -20 / $setFs + rem;
    margin-bottom: 10 / $setFs + rem;
    position: relative;

    @media screen and (max-width: 768px) {
        margin-left: 20 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        margin-left: 10 / $setFs + rem;
    }
}