.hashtag_list {
    margin-top: 50 / $setFs + rem;
    display: flex;
    flex-wrap: wrap;
}

.hashtag_li {
    margin-right: 20 / $setFs + rem;
    margin-bottom: 10 / $setFs + rem;
}

.hashtag_link {
    font-size: ($setFs + 1) / $setFs + rem;
    font-weight: bold;
    line-height: (26/16);
    @include acc;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (16 + 3) / $setFs + rem;
    }

    &:before {
        content: "#";
        margin-right: 3 / $setFs + rem;
    }

    &:hover {
        color: $color;
    }
}