.date {
    display: flex;

    .from,
    .to {
        display: flex;
        align-items: center;
        font-family: $font1;
        letter-spacing: 2 / $setFs + rem;
    }

    .to {
        margin-left: 65 / $setFs + rem;
        position: relative;

        &:before {
            content: "";
            width: 56 / $setFs + rem;
            height: 1 / $setFs + rem;
            background: #dbdbdb;
            position: absolute;
            top: 50%;
            right: 100%;
            margin-right: 15 / $setFs + rem;
        }
    }

    .year {
        flex: 0 0 26 / $setFs + rem;
        font-size: 18 / $setFs + rem;
        letter-spacing: 1 / $setFs + rem;
        line-height: 0.85;
        word-break: break-all;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (18 + 3) / $setFs + rem;
        }
    }

    .date {
        font-size: 36 / $setFs + rem;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (36 + 3) / $setFs + rem;
        }
    }
}

.duration {
    @extend .date;
}