.ck_editor {
    font-size: ($setFs + 1) / $setFs + rem;
    line-height: (40 / 21);
    text-align: justify;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: ($setFs + 3) / $setFs + rem;
    }

    a {
        color: #0070c0;
        display: inline;
        position: relative;

        &:hover {
            text-decoration: underline;
            // &::before {
            //     display: none;
            // }
        }

        // &::before {
        //     content: "";
        //     width: 100%;
        //     border-bottom: 1 / $setFs + rem dotted #000;
        //     position: absolute;
        //     bottom: -1 / $setFs + rem;
        //     left: 0;
        // }
    }

    h2 {
        font-size: 20 / $setFs + rem;
        margin-bottom: 15 / $setFs + rem;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (20 + 3) / $setFs + rem;
        }
    }

    p {
        margin-bottom: 20 / $setFs + rem;

        &:last-child {
            margin-bottom: 0;
        }
    }

    ul {
        list-style-type: disc;

        li {
            margin-left: 18 / $setFs + rem;
        }
    }

    iframe {
        height: 450 / $setFs + rem;
        margin-bottom: 30 / $setFs + rem;
    }

    img {
        max-width: 800 / $setFs + rem;
        margin: 0 auto;
        border: 10 / $setFs + rem solid #dbdbdb;
        box-sizing: border-box;
    }

    figure {
        max-width: 26rem;
        float: left;
        margin-right: 50 / $setFs + rem;
        margin-top: 10 / $setFs + rem;
        margin-bottom: 10 / $setFs + rem;
    }

    figcaption {
        font-size: 0.9rem;
        color: #9e9e9e;
        text-align: center;
        position: relative;
        margin-top: 5 / $setFs + rem;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (14 + 2) / $setFs + rem;
        }
    }
}

.img-responsive {
    display: inline-block;
}