.page_banner {
    .container {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
    }
}

.banner_inner {
    color: #fff;

    .mainTitle_row {

        &.center.line {
            .zh:after {
                background: #fff;
            }
        }
    }
}

.banner_txt {
    font-size: 18 / $setFs + rem;
    text-align: center;
    line-height: (32/18);
    margin-top: 30 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (18 + 3) / $setFs + rem;
    }

    p {
        margin-bottom: 30 / $setFs + rem;
    }
}